/*----------- product_card ------------*/
.cardProduct .flex14 {
	display:flex;
	flex-flow: wrap;
}
.flex14 .blockLeft {
	//width:100%;
	margin: 0 10px;
}
.flex14 .tabs {
	//width: 400px;
	height:200px;
	margin:15px auto;
}
.tabs .listItemProd {
	display:flex;
	width: 100%;
	justify-content: space-between;
}
.listItemProd a {
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 110%;
	letter-spacing: 0.05em;
	color: #5C5C5C;
}
.blockTable td {
	width: auto;
}
.blockLeft .blockImg2 {
	display: flex;
	width:300px;
	flex-flow: wrap;
	justify-content: space-between;
}
.blockImg2 .imgProd3 {
	width: 300px;
	margin: 2px;
}
.blockImg2 .mini {
	width: 95px;
}
.blockSize .sizeProd {
	width:34px;
	height:31px;
	background: #FFFFFF;
	border: 1px solid #E5E5E5;
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 19px;
	text-align: center;
	color: #5C5C5C;
	padding:5px 0;
}
.blockRight .blockUp {
	display: flex;
	flex-flow: wrap;
	margin: 0 10px;
    padding-bottom: 10px;
    border-bottom: .1rem solid #F0F0F0;
}
.blockUp h1 {
	font:normal 2.7rem 'AvenirNextCyr';
	line-height: 44px;
	color: #5C5C5C;
	width: 95%;
	text-align: left;
}
.blockSize h4 {
	margin:10px 0;
}
.blockSize ul {
	display: flex;
	flex-flow: wrap;
	margin:10px 0;
}
.blockSize ul li {
	margin:5px;
}
.blockUp .yesProd1 {
	width: auto;
	height: max-content;
	border: 1px solid #52BD10;
	padding:10px;
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 110%;
	color: #52BD10;
}
.blockRight .blockMedium {
	display: flex;
}
.blockMedium .blockSize,
.blockMedium .blockPrice {
	width: 50%;
	margin: 10px;
}
.blockMedium1 .blockPay1 .payLink1 {
	display: inline-block;
}
.blockMedium .blockPrice {
	//width: 120px;
	margin: 10px;
}
.blockMedium .blockPrice .size1 {
	font-size: 3rem;
}
.blockPrice .blockOldSize {
	display: flex;
	align-items: center;
	justify-content:flex-end;
}
.blockMedium .blockPrice .oldSize1 {
	margin-right: 10px;
	text-align: right;
}
.blockOldSize .labelSale {
	//position: absolute;
	width: 35px;
	height:35px;
	background: url('../img/labelSale_icon.png') center no-repeat;
}
.blockMedium .blockPrice p {
	text-align: right;
}
.blockRight .blockMedium1 {
	display: flex;
	flex-flow: wrap;
	margin:5px;
	justify-content: flex-end;
}
.blockMedium1 .blockPay1 .payLink1 {
	text-align:right;
	background:#F4A876;
	color:#fff;
}
.formCallback #backPhone {
	display: flex;
	flex-flow: wrap;
	margin-bottom: 10px;
}
#backPhone input {
	width: 50%;
	padding: 15px;
}
#backPhone input[type="telephone"] {
	border: 1px solid #E5E5E5;
}
#backPhone input[type="submit"] {
	color: #F4A876;
	background: #fff;
	border: 1px solid #F4A876;
}
.blockRight .blockTable {
	margin:15px 0;
}
.blockTable table {
	width: 100%;
	text-align: left;
	background: rgba(244, 168, 118, 0.05);
}
.blockTable table {
	font:normal 1.2rem 'AvenirNextCyr';
	line-height: 19px;
	color: #C4C4C4;
	padding: 10px;
}
.blockTable table tr {
	margin:10px 0;
}
.blockTable table td {
	margin:0;
}
.blockTable table tr td:last-child {
	text-align: left;
}
.tabs .listItemProd {
	margin-bottom: 20px;
}
.listItemProd li {
	padding:10px;
}
#fragment-4 .textEmail input {
	border: 1px solid #E5E5E5;
	padding:15px;
}
/*.ui-tabs-tab, .ui-corner-top, .ui-state-default, .ui-tab,*/
.ui-tabs-active,
.ui-state-active,
.ui-state-focus {
	border: 1px solid #F4A876;
	color:#F4A876 ;
}





/*---------------------- @media --------------------------*/
@media screen and (min-width:667px) {
	.blockRight .blockMedium1 {
		justify-content:space-between;
	}
	.blockRight .blockUp {
		flex-flow: row;
		justify-content: space-between;
	}
	.blockUp h1 {
		width: 70%;
	}
}
@media screen and (min-width:768px) {
	.listItemProd a {
		font-size: 1.8rem;
	}
	.blockTable table {
		font-size: 1.4rem;
	}
}
@media screen and (min-width:1024) {
	.flex14 .blockLeft,
	.flex14 .blockRight {
		width: 50%;
	}
	.cardProduct .flex14 {
		flex-flow: row;
	}
}
@media screen and (min-width:1280) {}
@media screen and (min-width:1360) {}
